// assets/scripts/SuccessPopup.ts

const { ccclass, property } = cc._decorator;

@ccclass('SuccessPopup')
export default class SuccessPopup extends cc.Component {
  @property(cc.Label)
  messageLabel: cc.Label = null;

  public onOk: (() => void) | null = null;

  onLoad() {
    const okBtnNode = cc.find('DialogBox/OKButton', this.node);
    if (okBtnNode) {
      const okBtn = okBtnNode.getComponent(cc.Button);
      if (okBtn) {
        okBtn.node.on('click', () => {
          if (this.onOk) {
            this.onOk();
          }
          this.node.destroy();
        });
      }
    }
  }
}
